.proposal {
  padding-top: 40px;
  padding-bottom: 72px;
  padding-left: 16px;

  @media (min-width: $tablet-width) {
    padding-bottom: 104px;
    padding-left: 24px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 80px;
    padding-left: 0;
  }

  > *:not(.proposal__wrapper):not(.proposal__projects):not(.proposal__tab-item) {
    margin-right: 16px;

    @media (min-width: $tablet-width) {
      margin-right: 24px;
    }

    @media (min-width: $desktop-width) {
      margin-right: 0;
    }
  }

  // > *:not(.proposal__projects) {
  //   margin-right: 16px;

  //   @media (min-width: $tablet-width) {
  //     margin-right: 24px;
  //   }

  //   @media (min-width: $desktop-width) {
  //     margin-right: 0;
  //   }
  // }

  &--aside {
    @media (min-width: $desktop-width) {
      display: grid;
      grid-template-columns: minmax(740px, 1fr) 360px;
      grid-column-gap: 20px;
      grid-template-areas:
        "a a"
        "b b"
        "c c"
        "curator curator"
        "rules rules"
        "d e"
        "f k"
        "g l"
        "h m"
        "i i"
        "j j"
        "n n";

      >* {
        align-self: start;
      }
    }
  }
}

//       -
.proposal__buttons {
  grid-area: i;
}

//grid-area: a
.proposal__info {
  margin-top: 0;
  margin-bottom: 32px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    grid-area: a;
    margin-bottom: 40px;
  }
}

.proposal__comments {
  margin-bottom: 16px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 12px;
  }
}

//grid-area: b
.proposal__title {
  margin-bottom: 24px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 40px;
  }

  @media (min-width: $desktop-width) {
    grid-area: b;
  }
}

//grid-area: curator
.proposal__curator {
  margin-bottom: 24px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 40px;
  }

  @media (min-width: $desktop-width) {
    grid-area: curator;
  }
}

.proposal__vote {
  margin-bottom: 32px;

  @media (min-width: $tablet-width) {
    margin-bottom: 40px;
  }

  @media (min-width: $desktop-width) {
    position: sticky;
    top: 115px;
    grid-area: e;
  }
}

.proposal__vote-period {
  color: var(--color-text, $grey-default);
  font-size: rem(16px);
  line-height: 135%;
  font-weight: 400;
}

.proposal__vote-dates {
  color: var(--color-text, $dark-text);
}

.proposal__text-container {
  margin-bottom: 48px;

  @media (min-width: $desktop-width) {
    margin-bottom: 40px;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 64px;
  }
}

.proposal__text-container p {
  margin: 0;
  line-height: 180%;
}

//grid-area: d
.proposal__wrapper {
  @media (min-width: $desktop-width) {
    grid-area: d;
  }

  >*:not(.proposal__slider) {
    margin-right: 16px;

    @media (min-width: $tablet-width) {
      margin-right: 24px;
    }

    @media (min-width: $desktop-width) {
      margin-right: 0;
    }
  }
}

.proposal__last-comment {
  margin-bottom: 56px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 64px;
  }
}

.proposal__form-title {
  margin-bottom: 32px;

  @media (min-width: $tablet-width) {
    margin-bottom: 40px;
  }
}

.proposal__description {
  margin-bottom: 72px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 80px;
  }
}

.proposal__image-uploads {
  margin-bottom: 72px;
  position: relative;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 80px;
  }
}

.proposal__map {
  margin-bottom: 24px;
  overflow: hidden;

  @media (min-width: $tablet-width) {
    margin-bottom: 48px;
  }

  &--no-address {
    .map-section__address {
      display: none;
    }
  }
}

.proposal__slider {
  margin-bottom: 56px;

  @media (min-width: $desktop-width) {
    margin-bottom: 48px;
  }

  @media (min-width: $tablet-width) {
    margin-bottom: 72px;
  }

  h2 {
    margin: 0 0 24px;
  }
}

.proposal__gallery {
  max-width: 740px;

  @media (max-width: $desktop-width - 1) {
    max-width: unset;
  }

  &--bordered {
    margin-bottom: 40px;
    padding-bottom: 48px;
    border-bottom: 1px solid var(--color-stroke, $light-grey);

    @media (min-width: $desktop-width) {
      margin-bottom: 48px;
    }
  }

  .gallery__tape .swiper-slide:first-child {
    padding-left: 0;
  }
}

.proposal__address {
  margin-bottom: 34px;
}

.proposal__status {
  grid-area: c;
  padding: 15px 24px;
  border: 1px solid var(--color-stroke, $light-grey);
  border-radius: $border-radius;
  margin-bottom: 39px;

  @media (max-width: $desktop-width - 1) {
    margin-bottom: 23px;
  }

  @media (max-width: $tablet-width - 1) {
    margin-bottom: 26px;
  }

  p {
    margin: 0;

    font-weight: 500;
    font-size: rem(15px);
    line-height: 135%;

    span {
      margin-left: 5px;
      font-weight: 400;
      color: var(--color-text, $green);
    }
  }

  &--files {
    display: flex;
    justify-content: space-between;

    @media (max-width: $tablet-width) {
      flex-direction: column;
    }

    p {
      margin-top: 3px;
    }

    .files {
      width: auto;
      grid-gap: 32px;
      margin-left: 25px;

      @media (max-width: $desktop-width) {
        grid-gap: 16px;
      }

      @media (max-width: $tablet-width) {
        margin-left: 0;
        margin-top: 16px;
      }

      .file-item {
        width: 270px;
        padding: 0;
        border: none;

        @media (max-width: $desktop-width) {
          width: 200px;
        }

        @media (max-width: $tablet-width) {
          width: auto;
        }
      }

      .file-item__name:not(.hack-class) {
        color: var(--color-text, $dark-text);
      }
    }
  }

  &--orange {
    p span {
      color: var(--color-text, $orange);
    }
  }
}

.proposal__files {
  margin-bottom: 48px;

  h2 {
    margin: 0 0 25px;
  }
}

.proposal .project-rules {
  grid-area: rules;
  &:not(:last-child) {
    margin-bottom: 40px;
  }
}

//   -,   ,
//   proposal__wrapper grid-area d   ,
//      .proposal__projects-wrapper
.proposal__projects {
  grid-area: f;

  @media (max-width: $mobile-max-width) {
    margin-right: 0;
  }
}

.proposal__tabs {
  display: flex;
  border-bottom: 1px solid var(--color-text, $light-grey);
  margin-bottom: 38px;
  grid-area: g;

  @media (max-width: $desktop-width - 1) {
    //margin-left: 24px;
    margin-right: 24px;
  }

  @media (max-width: $tablet-width) {
    justify-content: space-around;
    margin-right: 16px;
  }

  @media (max-width: 415px) {
    //margin-right: 20px;
    //margin-right: 0;
    justify-content: space-around;
  }

  @media (max-width: $mobile-max-width) {
    margin-right: 16px;
    //margin-left: 16px;
    justify-content: space-between;
  }
}
// .proposal {
//   form {
//     grid-area: h;
//   }
// }
.proposal__projectstab,
.proposal__milestonestab {
  @media (min-width: $desktop-width) {
    grid-area: h;
    //visibility: hidden;
    display: none;
  }
  //height: 0;
  //visibility: hidden;
  display: none;

  @media (max-width: $tablet-width) {
    //margin-left: 16px;
    margin-right: 16px;
  }
  @media (max-width: $mobile-max-width) {
    //margin-left: 16px;
    margin-right: 0;
  }

  .proposal__project-mark {
    margin-left: 0;
    margin-left: 32px;

    @media (max-width: $mobile-max-width) {
      margin-left: 0;
    }
  }
}

.proposal__tab-item {
  .form-radio label::before {
    display: none;
  }
}

.proposal__tab-item--active {
  @media (min-width: $desktop-width) {
    //visibility: visible;
    //z-index: 100;
    display: block;
  }
  //height: auto;
  //visibility: visible;

  .form-radio label::before {
    display: block;
  }
}

.proposal__tabs .proposal__tab {
  border-radius: 0;
  padding-left: 0;
  padding-right: 0;
  border: none;
  outline: none;
  background-color: transparent;
  font-size: 24px;
  line-height: 28.44px;
  font-weight: 400;
  color: var(--color-text, $grey);
  padding-bottom: 13px;
  margin-right: 40px;
  border-bottom: 1px solid transparent;
  text-transform: none;

  @media (max-width: $tablet-width) {
    margin-right: 0;
    font-size: 16px;
    padding-bottom: 5px;
    margin: 0 auto;
  }

  @media (max-width: 415px) {
    //margin-right: 20px;
    //margin-right: 0;
    font-size: 16px;
    padding-bottom: 5px;
    margin: 0 auto;
  }

  @media (max-width: 415px) {
    //margin-right: 20px;
    //margin-right: 0;
    font-size: 16px;
    padding-bottom: 5px;
    margin: 0 auto;
  }

  @media (max-width: $mobile-max-width) {
    //margin-right: 20px;
    margin-right: 0;
    font-size: 16px;
    padding-bottom: 5px;
  }

  &--active {
    border-bottom: 1px solid var(--color-interactive-default, $blue-default);
    color: var(--color-interactive-default, $blue-default);
    cursor: unset;
  }

  transition: opacity 0.3s ease-out;

  @media (hover: hover), screen and (min-width: 0\0) {
    &:hover,
    &:focus {
      opacity: 1;
    }
  }
}

.proposal__milestonestab {
  margin-bottom: 22px;
}

.proposal__projects {
  margin-bottom: 22px;
  @media (max-width: $desktop-width - 1) {
    margin-bottom: 41px;
    //margin-left: $tablet-padding;
    //margin-right: $tablet-padding;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 34px;
    //margin-left: $tablet-padding;
    //margin-right: $tablet-padding;
  }

  @media (max-width: $mobile-max-width) {
    margin-bottom: 34px;
    margin-left: 0;
    margin-right: 0;
  }

  h2 {
    margin: 0;
    margin-top: -5px;
    margin-bottom: 40px;

    @media (max-width: $mobile-max-width) {
      margin-bottom: 34px;
      //margin-left: 16px;
      margin-right: 16px;
    }
  }
}

.proposal__project-wrapper {
  display: flex;
  width: 100%;

  .proposal__project-img,
  .proposal__project-link,
  .proposal__project-figure {
    width: 230px;
    height: 140px;
  }

  .proposal__project-link {
    @media (max-width: $tablet-width) {
      width: 100%;
    }
    @media (max-width: $mobile-max-width) {
      width: 100%;
    }
  }

  .proposal__item-img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: $border-radius;
  }

  .proposal__project-figure {
    margin: 0;
    @media (max-width: $tablet-width) {
      width: 100%;
    }
    @media (max-width: $mobile-max-width) {
      width: 100%;
    }
  }

  .proposal__project-img {
    //margin-left: auto;
    margin-left: 40px;
    margin-bottom: 16px;

    @media (max-width: $tablet-width) {
      margin-left: unset;
      width: 100%;
      order: 1;
      margin-bottom: 24px;
    }
    @media (max-width: $mobile-max-width) {
      margin-left: unset;
      width: 100%;
      order: 1;
      margin-bottom: 24px;
    }

    .proposal__project-votes {
      display: inline-block;
      margin-bottom: 5px;
      font-size: 13px;
      line-height: 16.9px;
      color: var(--color-text, $grey);
      margin-left: auto;
      align-self: baseline;
      @media (max-width: $tablet-width) {
        margin-right: auto;
        margin-left: 32px;
      }
      @media (max-width: $mobile-max-width) {
        margin-right: auto;
        margin-left: 32px;
      }

      &--desktop {
        @media (max-width: $tablet-width) {
          display: none;
        }
      }
    }
  }
  @media (min-width: $tablet-width) {
    margin-right: 24px;
  }
  @media (max-width: $tablet-width) {
    flex-direction: column-reverse;
    margin-right: 16px;
  }
  @media (max-width: $mobile-max-width) {
    margin-right: 16px;
    flex-direction: column-reverse;
    display: block;
    display: flex;
    //margin-left: 16px;
    //margin-right: 16px;
    width: unset;
  }
}

.proposal__project-details,
.proposal__milestones-details {
  span {
    font-size: 15px;
    line-height: 20.25px;
    color: var(--color-text, $blue-default);
    border-bottom: 1px dashed var(--color-text, #9BD2F4);
  }

  svg {
    margin-left: 9px;
  }
}

.proposal__project {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding-bottom: 32px;
  border-bottom: 1px solid var(--color-text, $light-grey);
  margin-bottom: 33px;
  position: relative;

  // @media (max-width: $mobile-max-width) {
  //   padding-bottom: 60px;
  // }

  .proposal__gallery {
    display: block;
    max-height: 0;
    opacity: 0;
    transition: max-height 0.3s ease-in-out, opacity 0.3s ease-in-out;

    &.opened {
      opacity: 1;
      max-height: 100em;
    }

    @media (max-width: $mobile-max-width) {
      margin-left: 0;
      //margin-left: 16px;
    }
  }

  .button--blue {
    margin-top: 32px;
    align-self: start;
    display: none;
    padding-top: 11px;
    padding-bottom: 11px;
    //display: block;
    margin-left: 32px;
    max-height: 0;
    opacity: 0;
    transition: max-height 0.3s ease-in-out, opacity 0.3s ease-in-out;

    &.opened {
      display: block;
      opacity: 1;
      max-height: 100em;
    }

    @media (max-width: $tablet-width) {
      margin-left: 30px;
    }

    @include mobile {
      margin-left: 0;
      width: unset;
    }
  }

  //  
  .proposal-list__like-counter {
    margin-top: 32px;
    display: flex;
    align-items: center;
    display: none;
    display: flex;

    span {
      font-size: 18px;
      line-height: 23.4px;
      font-weight: 400;
      color: var(--color-text, $dark-text);
    }

    .form-checkbox {
      margin-right: 10px;
    }
  }

  &_enough-lines_false {
    .proposal__project-text {
      height: auto;

      &:after {
        display: none;
      }
    }

    .button-details {
      display: none;
    }
  }

  .proposal__project-text {
    max-height: 4.3em;
    margin-bottom: 23px;
    transition: max-height 0.3s ease-in-out;

    @media (max-width: $mobile-max-width) {
      max-height: 4.2em;
      margin-bottom: 9px;
      margin-bottom: 20px;
    }

    &.opened {
      max-height: 100em;

      &:after {
        display: none;
      }
    }

    &:after {
      content: "";
      position: absolute;
      //top: -48px;
      left: 0;
      width: 100%;
      height: 48px;
      background-image: linear-gradient(180deg, var(--color-bg-main, $white) 0%, var(--color-bg-main-transparent, rgba($white, 0)) 100%);
      transform: matrix(1, 0, 0, -1, 0, 0);
      bottom: 50px;
      bottom: 0;
    }

    // &:after {
    //   position: absolute;
    //   bottom: -0.4rem;
    //   left: 0;

    //   width: 100%;
    //   height: 2rem;

    //   background: linear-gradient(
    //     to bottom,
    //     rgba(255, 255, 255, 0),
    //     var(--color-bg-secondary, $color-bg) 100%
    //   );

    //   text-align: right;

    //   content: '';
    // }
  }

  &.project_enough-lines_false .proposal__project-text::after {
    display: none;
  }

  &_enough-lines_true {
    .proposal__project-title {
      + .proposal__project-text {
        height: 2.6em;
        margin-bottom: 14px;

        @media (max-width: $mobile-max-width) {
          height: 8em;
          margin-bottom: 9px;
        }

        &:after {
          bottom: -0.1rem;
        }
      }
    }

    //span  
    .button-details__text {
      height: 3.4rem;

      @media (max-width: $mobile-max-width) {
        height: 8.4rem;

        font-size: rem(13px);
        line-height: 1.35;
      }

      &.opened {
        height: auto;

        &:after {
          display: none;
        }
      }

      //     
      &:after {
        position: absolute;
        bottom: -0.4rem;
        left: 0;

        width: 100%;
        height: 2rem;

        background: linear-gradient(
          to bottom,
          rgba(255, 255, 255, 0),
          var(--color-bg-secondary, $color-bg) 100%
        );

        text-align: right;

        content: '';
      }
    }

    .button-details {
      display: initial;
    }
  }

  // -
  .button-details {
    margin-left: 30px;
    align-self: start;
    display: block;
    padding: 0;
    cursor: pointer;
    border: none;
    outline: none;
    background-color: transparent;
    z-index: 1;

    @media (max-width: $mobile-max-width) {
      //bottom: 0;
      left: 0;
      margin-left: 0;
      //margin-bottom: 30px;
      margin-left: 16px;

    }

    transition: opacity 0.3s ease-out;

    @media (hover: hover), screen and (min-width: 0\0) {
      &:hover,
      &:focus {
        opacity: 0.7;
      }
    }

    &__text {
      margin-right: 0;

      &_opened {
        display: none;
      }

      &_closed {
        display: inline-block;
      }
    }

    .button-details__icon {
      width: 14px;
      height: 8px;
      transform: rotate(0deg);
    }

    &.opened {
      margin-top: 32px;

      @include mobile {
        margin-left: 0;

        //margin-left: 16px;
      }

      .button-details__text_opened {
        display: inline-block;
      }

      .button-details__text_closed {
        display: none;
      }

      .button-details__icon {
        width: 14px;
        height: 8px;
        transform: rotate(180deg);
      }

      @media (hover: hover), screen and (min-width: 0\0) {
        &:hover,
        &:focus {
          opacity: 1;
          //cursor: unset;
        }
      }
    }
  }

  .proposal__milestones-details {
    bottom: 32px;
    bottom: -25px;
  }

  //   
  .proposal__project-text {
    position: relative;
    color: var(--color-text, $dark-text);
    font-size: 15px;
    line-height: 20.25px;
    padding-left: 32px;

    overflow: hidden;

    @media (max-width: $tablet-width) {
      padding-left: 0;
      padding-left: 32px;
      width: 100%;
    }

    @include mobile {
      padding-left: 0;
      width: 100%;
    }
  }

  .proposal__project-text--stage {
    padding-left: 0;
    margin-bottom: 30px;
    margin-bottom: 20px;
  }
}

.form-radio input[type="radio"]:checked ~ label {

  .proposal__project {
    position: relative;
    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 2px;
      height: calc(100% - 32px * 2);
      height: 110px;
      background-color: $blue-default;
      top: 30px;
      left: 10px;
      z-index: 100;
      @media (max-width: $tablet-width) {
        left: -15px;
        top: 0;
      }
      @media (max-width: $mobile-max-width) {
        left: 8px;
      }
      @media (max-width: $mobile-width) {
        left: 7px;
      }
    }
  }
}

.form-radio input[type="radio"]:checked ~ label {

  .proposal__project {
    position: relative;
    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 2px;
      //height: calc(100% - 32px * 2);
      //height: calc(110px + 60%);
      background-color: $blue-default;
      top: 30px;
      left: 10px;
      z-index: 100;
      @media (max-width: $tablet-width) {
        left: -15px;
        top: 0;
      }
      @media (max-width: $mobile-max-width) {
        left: 8px;
      }
      @media (max-width: $mobile-width) {
        left: 7px;
      }
    }
  }
}

.proposal__project .swiper-slide {
  //margin-right: 15px;
  width: 129px;
  height: 129px;
}

.proposal__project {
  .proposal__gallery {
    padding-left: 32px;

    @media (max-width: $tablet-width) {
      padding-left: 0;
      width: 100%;
    }

    @media (max-width: $mobile-max-width) {
      padding-left: 0;
      width: 100%;
    }

    .gallery__item,
    .gallery__item-link {
      width: 129px;
      height: 129px;
    }
  }
  .proposal__project-details {
    margin-top: 32px;
    margin-top: -20px;
    margin-left: 32px;

    @include mobile {
      margin-left: 0;
      margin-top: 0;
    }

    svg {
      width: 14px;
      height: 8px;
      transform: rotate(-180deg);
    }
  }

  .proposal__gallery--stage {
    padding-left: 0;

    .gallery__item-link,
    .gallery__item,
    .swiper-slide {
      width: 135px;
      height: 135px;
    }
  }
  // .gallery__tape .swiper-slide:first-child {
  //   @media (max-width: $tablet-width) {
  //     padding-left: 0;
  //   }
  //   @media (max-width: $mobile-max-width) {
  //     padding-left: 0;
  //   }
  // }
}

.proposal__milestones-item {
  .proposal__milestones-details {
    //margin-top: 26px;
    margin-left: 28px;
    margin-top: 0;

    svg {
      transform: rotate(-180deg);
    }

    span {
      margin-right: 0;
    }
  }

  .proposal__milestones-title {
    margin: 0;
    font-size: 18px;
    line-height: 23.4px;
    font-weight: 400;
  }
}

//    504
.proposal__project-desc {
  width: 470px;
  width: 100%;

  @media (max-width: $desktop-width - 1) {
    width: 100%;
    //margin-right: 32px;
  }
}

.proposal__project-head {
  display: flex;
  flex-direction: column;

  @media (max-width: $tablet-width) {
    flex-direction: column;
    //flex-wrap: wrap;
    text-align: left;
    align-items: baseline;
  }
  @media (max-width: $mobile-max-width) {
    flex-direction: column;
    //flex-wrap: wrap;
    text-align: left;
    align-items: baseline;
  }

  h3,
  .form-radio label {
    font-size: 18px;
    line-height: 23.4px;
    font-weight: 400;
    margin: 0;
    color: var(--color-text, $dark-text);
  }

  .proposal__project-votes {
    font-size: 13px;
    line-height: 16.9px;
    color: var(--color-text, $grey);
    margin-left: auto;
    align-self: baseline;
    @media (max-width: $tablet-width) {
      margin-right: auto;
      margin-left: 32px;
    }
    @media (max-width: $mobile-max-width) {
      margin-right: auto;
      margin-left: 32px;
    }

    &--mobile {
      display: none;

      @media (max-width: $tablet-width) {
        display: inline-block;
      }
    }
  }

  .form-radio {
    margin-bottom: 0;
    margin-right: 5px;
  }
}

.proposal__milestones-item {
  padding-bottom: 32px;
}
.proposal__milestones-item:nth-child(2n) {
  padding-bottom: 42px;
}

.proposal__milestones-item:nth-child(3) {
  margin-top: 32px;
  padding-bottom: 45px;

  .button-details {
    bottom: 34px;
  }
}

.proposal__milestones-item {
  .button-details {
    padding: 0;
    cursor: pointer;
    border: none;
    outline: none;
    background-color: transparent;
    margin-left: 0;

    &__text {
      margin-right: 9px;

      &_opened {
        display: none;
      }

      &_closed {
        display: inline-block;
      }
    }

    .button-details__icon {
      transform: rotate(0deg);
    }

    &.opened {
      .button-details__text_opened {
        display: inline-block;
      }

      .button-details__text_closed {
        display: none;
      }

      .button-details__icon {
        transform: rotate(180deg);
      }
    }
  }

  .proposal__milestones-date {
    font-size:  rem(14px);
    line-height: rem(18.2px);
    font-weight: 500;
    margin-bottom: 16px;
  }
}

.proposal__milestones-details.proposal__project--opened {
  &:before {
    display: none;
  }
}

.proposal__variant-wrapper {
  display: flex;
}

.proposal__project-mark {
  display: flex;
  //display: none;
  align-items: center;
  margin-top: 0;
  margin-left: 32px;
  max-height: 0;
  opacity: 0;
  transition: max-height 0.3s ease-in-out, opacity 0.3s ease-in-out;
  overflow: hidden;

  &.opened {
    max-height: 100em;
    margin-top: 32px;
    margin-left: 32px;
    opacity: 1;
    //margin-left: 32px;
  }

  @media (max-width: $mobile-max-width) {
    margin-left: 0;
    margin-right: 16px;
  }

  svg {
    width: 24px;
    height: 24px;
    margin-right: 10px;
  }

  font-size: 18px;
  line-height: 23.4px;

  @media (max-width: $mobile-max-width) {
    font-size: 14px;
  }
}

.proposal--aside .buttons-wrapper {
  grid-area: j;
}

.proposal--aside .buttons-wrapper--send {
  grid-area: n;
  display: flex;

  @media (max-width: $mobile-max-width) {
    flex-wrap: wrap;
  }
}

.proposal__project--active {
  &::after {
    content: "";
    position: absolute;
    top: 30px;
    bottom: 32px;
    left: 8px;
    background: var(--color-text, $blue-default);
    width: 2px;

    @media (max-width: $tablet-width) {
      top: 0;
      left: -7px;
      bottom: 35px;
    }
    @media (max-width: $mobile-max-width) {
      top: 0;
      left: -7px;
      bottom: 35px;
    }
    @media (max-width: $mobile-width) {
      top: 0;
      left: -7px;
      bottom: 40px;
    }
  }
}

.project__gallery-full {
  margin-left: 32px;
  margin-top: 16px;
  width: calc(100% - 32px);
  display: none;

  @include mobile {
    margin-left: 0;
    width: 100%;
  }

  @media (max-width: $mobile-max-width) {
    width: calc(100% - 16px);
  }

  .opened + &,
  &.opened {
    display: block;
  }
}

.form-radio input[type="radio"]:checked ~ label.proposal__project-title {
  font-size: 18px;
  line-height: 23.4px;
  font-weight: 400;
  margin: 0;
  color: var(--color-text, $dark-text);

  @media (max-width: $tablet-width) {
    max-width: unset;
  }

  &::before {
    margin-right: 11px;
    border: 6px solid var(--color-text, $blue-default);
  }
}

.button-details__icon {
  width: 14px;
  height: 8px;
  fill: var(--color-text, $blue-default);
  color: var(--color-text, $blue-default);
}

.proposal__banner {
  padding: 24px 32px 24px 68px;
  margin-bottom: 24px;

  @media (max-width: $desktop-width) {
    margin-right: 20px;
  }

  @media (max-width: $tablet-width) {
    padding-left: 54px;
    margin-right: 24px;
  }

  @media (max-width: $mobile-max-width) {
    padding-top: 12px;
    padding-right: 14px;
    padding-bottom: 16px;
    padding-left: 15px;
  }

  @media (max-width: 400px) {
    margin-right: 16px;
    margin-top: 34px;
  }

  .info-banner__icon {
    top: 26px;
  }
}

.proposal__markup {
  display: flex;
  align-items: center;

  padding-left: 28px;
  margin-top: 9px;

  color: var(--color-icon, $orange);

  @media (max-width: $tablet-width) {
    margin-bottom: 8px;
  }

  &--blue {
    color: var(--color-icon, $blue-default);
  }

  svg {
    color: inherit;
    width: 18px;
    height: 18px;
  }
}

.proposal__markup-text {
  color: inherit;
  font-size: 15px;
  line-height: 135%;
  margin-left: 4px;
}
