/*===============================
=            Choices            =
===============================*/
.choices {
  position: relative;
  margin-bottom: 24px;
  font-size: 16px;
  max-width: 675px;
}

.choices:focus {
  outline: none;
}

.choices:last-child {
  margin-bottom: 0;
}

.choices.is-disabled .choices__inner,
.choices.is-disabled .choices__input {
  background-color: var(--color-bg-secondary, #eaeaea);
  cursor: not-allowed;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.choices.is-disabled .choices__item {
  cursor: not-allowed;
}

.choices [hidden] {
  display: none !important;
}

.choices[data-type*='select-one'] {
  cursor: pointer;
}

.choices[data-type*='select-one'] .choices__inner {
  // padding-bottom: 7.5px;
}

.choices[data-type*='select-one'] .choices__input {
  display: block;
  width: 100%;
  //padding: 10px;
  //border-bottom: 1px solid #dddddd;
  color: var(--color-text, #354052);

  background-color: var(--color-bg-secondary, $white);
  margin: 0;

  position: absolute;
  top: -53px;
  padding: 16px 30px 16px 16px;
  font-size: 15px;
  line-height: 20px;
}

.choices[data-type*='select-one'] .choices__button {
  background-image: url(data:image/svg+xml;base64,);
  padding: 0;
  background-size: 8px;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -10px;
  margin-right: 25px;
  height: 20px;
  width: 20px;
  border-radius: 10em;
  opacity: 0.5;
}

.choices[data-type*='select-one'] .choices__button:hover, .choices[data-type*='select-one'] .choices__button:focus {
  opacity: 1;
}

.choices[data-type*='select-one'] .choices__button:focus {
  box-shadow: 0 0 0 2px #00bcd4;
}

.choices[data-type*='select-one'] .choices__item[data-value=''] .choices__button {
  display: none;
}

.choices[data-type*='select-one']:after {
  background: url('../img/icon-choices.svg') no-repeat center;
  content: '';
  height: 8px;
  position: absolute;
  pointer-events: none;
  width: 13px;
  right: 20px;
  top: 50%;
  transform: translateY(-50%) rotate(-180deg);
}

.choices[data-type*='select-one'].is-open:after {
  transform: translateY(-50%);
}

.choices[data-type*='select-one'][dir='rtl']:after {
  left: 11.5px;
  right: auto;
}

.choices[data-type*='select-one'][dir='rtl'] .choices__button {
  right: auto;
  left: 0;
  margin-left: 25px;
  margin-right: 0;
}

.choices[data-type*='select-multiple'] .choices__inner,
.choices[data-type*='text'] .choices__inner {
  cursor: text;
}

.choices[data-type*='select-multiple'] .choices__button,
.choices[data-type*='text'] .choices__button {
  position: relative;
  display: inline-block;
  margin-top: 0;
  margin-right: -4px;
  margin-bottom: 0;
  margin-left: 8px;
  padding-left: 16px;
  border-left: 1px solid #008fa1;
  background-image: url(data:image/svg+xml;base64,);
  background-size: 8px;
  width: 8px;
  line-height: 1;
  opacity: 0.75;
  border-radius: 0;
}

.choices[data-type*='select-multiple'] .choices__button:hover, .choices[data-type*='select-multiple'] .choices__button:focus,
.choices[data-type*='text'] .choices__button:hover,
.choices[data-type*='text'] .choices__button:focus {
  opacity: 1;
}

.choices__inner {
  border: 1px solid #e3e8ee;
  background-color: var(--color-bg-secondary, $white);
  border-radius: 5px 5px 0 0;
  color: var(--color-text, #354052);
  display: inline-block;
  font-family: inherit;
  font-size: 15px;
  line-height: 20px;
  overflow: hidden;
  padding: 16px 30px 16px 16px;
  vertical-align: middle;
  width: 100%;
}

.is-open .choices__inner {
  //background-color: #ffffff;
  //border: 2px solid #B03797;
  //border-radius: 30px 30px 0 0;
}

.is-open .choices__inner {
  // border-radius: 6px;
}

.is-flipped.is-open .choices__inner {
  border-radius: 0 0 2.5px 2.5px;
}

.choices__list {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

.choices__list--single {
  display: inline-block;
  padding: 0 16px 0 0;
  width: 100%;
}

[dir='rtl'] .choices__list--single {
  padding-right: 4px;
  padding-left: 16px;
}

.choices__list--single .choices__item {
  width: 100%;
}

.choices__list--multiple {
  display: inline;
}

.choices__list--multiple .choices__item {
  display: inline-block;
  vertical-align: middle;
  border-radius: 20px;
  padding: 4px 10px;
  font-size: 12px;
  font-weight: 500;
  margin-right: 3.75px;
  margin-bottom: 3.75px;
  background-color: #00bcd4;
  border: 1px solid #00a5bb;
  color: #ffffff;
  word-break: break-all;
  box-sizing: border-box;
}

.choices__list--multiple .choices__item[data-deletable] {
  padding-right: 5px;
}

[dir='rtl'] .choices__list--multiple .choices__item {
  margin-right: 0;
  margin-left: 3.75px;
}

.choices__list--multiple .choices__item.is-highlighted {
  background-color: #00a5bb;
  border: 1px solid #008fa1;
}

.is-disabled .choices__list--multiple .choices__item {
  background-color: #aaaaaa;
  border: 1px solid #919191;
}

.choices__list--dropdown {
  visibility: hidden;
  border-radius: 0 0 5px 5px;
  opacity: 0;
  position: absolute;
  width: 100%;
  background-color: var(--color-bg-secondary, $white);
  border: 1px solid #e3e8ee;
  top: 100%;
  //overflow: hidden;
  word-break: break-all;
  will-change: visibility;
  transition: all ease-out .2s;
  padding: 8px 0;
  z-index: 10;

  .choices__placeholder {
    display: none;
  }
}

.choices__list--dropdown.is-active {
  visibility: visible;
  opacity: 1;
  transition: all ease-out .2s;


}

.is-open .choices__list--dropdown {
  //border-color: #dddfe0;
}

.is-flipped .choices__list--dropdown {
  top: auto;
  bottom: 100%;
  margin-top: 0;
  margin-bottom: -1px;
  border-radius: 0.25rem 0.25rem 0 0;
}

.choices__list--dropdown .choices__list {
  position: relative;
  //max-height: 220px;
  max-height: 0;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  will-change: scroll-position;
  transition: all ease-out 0.15s;
}

.is-active.choices__list--dropdown {
   .choices__list {
    max-height: 220px;
  }
}

.choices__list--dropdown .choices__item {
  color: var(--color-text, #354052);

  background-color: var(--color-bg-secondary, $white);
  font-family: inherit;
  font-size: 15px;
  font-weight: 3400;
  line-height: 21px;
  position: relative;
  padding: 8px 16px;
}

[dir='rtl'] .choices__list--dropdown .choices__item {
  text-align: right;
}

@media (min-width: 640px) {
  .choices__list--dropdown .choices__item--selectable {
    // padding-right: 100px;
  }
  .choices__list--dropdown .choices__item--selectable:after {
    content: attr(data-select-text);
    font-size: 12px;
    opacity: 0;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
  }
  [dir='rtl'] .choices__list--dropdown .choices__item--selectable {
    text-align: right;
    padding-left: 100px;
    padding-right: 10px;
  }
  [dir='rtl'] .choices__list--dropdown .choices__item--selectable:after {
    right: auto;
    left: 10px;
  }
}

.choices__list--dropdown .choices__item--selectable.is-highlighted {
  background-color: #f5f6f7;
  color: #2c8ecc;
  transition: all ease-out 0.15s;
}

.choices__list--dropdown .choices__item--selectable.is-highlighted:after {
  opacity: 0.5;
}

.choices__item {
  cursor: default;
}

.choices__item--selectable {
  cursor: pointer;
  transition: all ease-out 0.15s;
}

.choices__item--disabled {
  cursor: not-allowed;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  opacity: 0.5;
}

.choices__heading {
  font-weight: 600;
  font-size: 12px;
  padding: 10px;
  border-bottom: 1px solid #f7f7f7;
  color: gray;
}

.choices__button {
  text-indent: -9999px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
}

.choices__button:focus {
  outline: none;
}

.choices__input {
  display: inline-block;
  vertical-align: baseline;
  background-color: var(--color-bg-secondary, #f9f9f9);

  font-size: 14px;
  margin-bottom: 5px;
  border: 0;
  border-radius: 0;
  max-width: 100%;
  padding: 4px 0 4px 2px;
}

.choices__input:focus {
  outline: 0;
}

[dir='rtl'] .choices__input {
  padding-right: 2px;
  padding-left: 0;
}

.choices__placeholder {
  color: var(--color-text, #354052);
  font-family: inherit;
  font-size: 16px;
  font-weight: 300;
  line-height: 19px;
}

.choices .scrollbar-track {
  width: 10px;
  background-color: transparent;
}

.choices .scrollbar-thumb {
  width: 6px;
  background-color: var(--color-bg-secondary, #e3e8ee);
  border-radius: 5px;
}

/*=====  End of Choices  ======*/
